@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500;700;800;900&display=swap);
@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}




@font-face {
    font-family: "Blankboard";
    src: url(/BetaFolksProject/static/media/Blankboard.2ca1967d.ttf);
}

.LogoText{
    font-family: "Blankboard";
}


.arrow {
  pointer-events: none;
}

.arrow__path {
  stroke: rgb(29, 160, 68);
  fill: transparent;
  stroke-dasharray: 6 6;
}

@media screen AND (max-width: 1350px) {
  .arrow {
    display: none;
  }
}

body.loading{overflow:hidden;height:100vh}.loader .loader-inner .image-block{position:absolute;-webkit-transform-origin:center;transform-origin:center;display:flex;align-items:center;justify-content:center}.loader .loader-inner .image-block.image-1{width:300px;left:16%;bottom:14%}@media(max-width: 480px){.loader .loader-inner .image-block.image-1{left:4%;bottom:28%}}.loader .loader-inner .image-block.image-3{width:300px;right:12%;top:8%}@media(max-width: 480px){.loader .loader-inner .image-block.image-3{top:16%;right:4%}}.loader .loader-inner .image-block.image-4{max-width:400px;width:40%;right:20%;bottom:10%}@media(max-width: 480px){.loader .loader-inner .image-block.image-4{right:6%;bottom:32%}}.loader .loader-inner .image-block.image-5{width:280px;left:14%;top:12%}@media(max-width: 480px){.loader .loader-inner .image-block.image-5{left:6%;top:18%}}.loader .loader-inner .image-block img{width:100%;object-fit:fill}.transition-image{position:absolute;top:0;left:0;height:100vh;width:100vw;display:flex;align-items:center;justify-content:center;z-index:-1}.transition-image img{width:600px;display:flex}.transition-image.final{display:block;top:-128px;position:relative;width:90%;margin:0 auto;z-index:-100}@media(max-width: 480px){.transition-image.final{top:-56px}}.transition-image.final img{width:100%;max-width:100%}
